@charset "UTF-8";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  margin: 0px;
}

.cabecera {
  background-color: rgb(27, 37, 128);
  width: 100%;
  position: sticky;
  padding: 10px;
}
.cabecera .cabecera__menu-navegacion-nav li {
  list-style-type: none;
  font-family: "Staatliches";
}
.cabecera .cabecera__menu-navegacion-nav a {
  text-decoration: none;
  color: white;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera {
  font-size: 14px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
  width: 79%;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa .menu-hamburguesa__enlaceRegistro {
  width: 50px;
  border-radius: 0.5em;
  padding: 6px;
  color: rgb(27, 37, 128);
  background-color: rgb(204, 166, 31);
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__nav-label, .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__nav-input {
  /* Aquí utilizamos el display none para ocultar el menú
  hamburguesa cuando se encuentra en vista ordenador */
  display: none;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .nav-label__icono-hamburguesa {
  width: 30px;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__contenedor-logo-y-nombre {
  color: white;
  font-family: "Staatliches";
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__contenedor-logo-y-nombre .contenedor-logo-y-nombre__logo-pagina {
  width: 45px;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__contenedor-logo-y-nombre .contenedor-logo-y-nombre__nombre-pagina {
  color: white;
  /* Aquí utilizamos un display inline-block para
  que el nombre de Coinbase se coloque justo al lado
  del logo de la página y no debajo */
  display: inline-block;
}

@media screen and (min-width: 378px) and (max-width: 520px) {
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__nav-label {
    display: block;
    /* Aquí especificamos que se ponga el puntero seleccionando
    la imagen del menú hamburguesa cuando pasamos por encima  */
    cursor: pointer;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    /* Con el column indicamos que cada elemento de la lista
    se pone uno debajo de otro */
    flex-flow: column wrap;
    background-color: rgb(204, 166, 31);
    margin-left: 250px;
    margin-right: 20px;
    margin-top: 210px;
    width: 160px;
    height: 250px;
    position: absolute;
    /* Con clip-path indicamos que se oculte
    el menú mientras no pulsemos el botón
    y después indicamos que la
    transición dura 1 segundo y que se produzca a la entrada
    y a la salida */
    clip-path: circle(0 at center);
    transition: clip-path 1s ease-in-out;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa li a {
    color: black;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa .menu-hamburguesa__enlaceRegistro {
    color: rgb(0, 0, 0);
    background-color: rgb(204, 166, 31);
    border-radius: 0;
    padding: 0;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .nav-label__icono-hamburguesa {
    display: flex;
    top: 0;
    right: 0;
    padding: 5px;
    cursor: pointer;
    background-color: rgb(204, 166, 31);
    position: relative;
    /* Con el z-index hacemos que el icono de hamburguesa
    se posicione por encima del menú hamburguesa */
    z-index: 1;
  }
  .cabecera .cabecera__menu-navegacion-nav .lista-cabecera__nav-input:checked + .lista-cabecera__menu-hamburguesa {
    /* Indicamos que aparezca el menú y que vaya cerrándose
    desde el 100% de tamaño hacia el centro
    y que se abra desde el centro hasta el 100% de tamaño cuando
    pulsamos el input de tipo checkbox */
    clip-path: circle(100% at center);
  }
}
@media screen and (max-width: 378px) {
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera {
    position: relative;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__nav-label {
    display: block;
    cursor: pointer;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa {
    flex-flow: column wrap;
    background-color: rgb(204, 166, 31);
    margin-left: 180px;
    margin-top: 220px;
    width: 130px;
    height: 250px;
    padding-top: 35px;
    padding-bottom: 35px;
    position: fixed;
    clip-path: circle(0 at center);
    transition: clip-path 1s ease-in-out;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa li a {
    color: black;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa .menu-hamburguesa__enlaceRegistro {
    color: rgb(0, 0, 0);
    background-color: rgb(204, 166, 31);
    border-radius: 0;
    padding: 0;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .nav-label__icono-hamburguesa {
    display: flex;
    top: 0;
    right: 0;
    padding: 5px;
    cursor: pointer;
    background-color: rgb(204, 166, 31);
    position: absolute;
    z-index: 1;
  }
  .cabecera .cabecera__menu-navegacion-nav .lista-cabecera__nav-input:checked + .lista-cabecera__menu-hamburguesa {
    clip-path: circle(100% at center);
  }
}
