.main-pagina-producto {
  background-color: rgb(105, 120, 255);
  width: 100%;
  /* 100% de la altura del viewport
  El viewport es la parte visible del documento */
  min-height: 100vh;
  display: flex;
  /* Lo que hace la propiedad flex-direction column
  es indicar que los elementos de la lista se coloquen
  uno debajo del otro */
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main-pagina-producto .section__contenedor-producto {
  background-color: rgb(27, 37, 128);
  width: 574px;
  padding: 20px;
  margin: 20px;
}
.main-pagina-producto .section__contenedor-producto .contenedor-producto__logo-bitcoin-y-titulo {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
}
.main-pagina-producto .section__contenedor-producto .contenedor-producto__logo-bitcoin-y-titulo h2 {
  font-size: 30px;
  font-family: "Staatliches";
  color: white;
  padding-top: 36px;
}
.main-pagina-producto .section__contenedor-producto .contenedor-producto__logo-bitcoin-y-titulo img {
  width: 40px;
  /* La propiedad align-self lo que hace es mover en el 
  eje vertical un único elemento, en este caso coloca
  el elemento al final del eje vertical  */
  align-self: flex-end;
}
.main-pagina-producto .section__contenedor-producto #contenedor-producto__crypto-widget-coin-list {
  width: 54%;
  /* La propiedad margin 0 auto lo que hace en este caso
  es centrar el elemento de manera horizontal  */
  margin: 0 auto;
  margin-top: 20px;
}
.main-pagina-producto .section__contenedor-producto p {
  font-size: 16px;
  color: white;
  font-family: "Inter", sans-serif;
  /* La propiedad display inline lo que hace es colocar
  en linea en este caso un elemento en bloque por lo que
  el párrafo no pasaría debajo del anterior elemento  */
  display: inline;
}
.main-pagina-producto .section__contenedor-producto h3 {
  color: white;
  font-size: 16px;
  font-family: "Staatliches";
  display: inline;
}
.main-pagina-producto .section__contenedor-producto .contenedor-producto__info-crypto-bitcoin {
  padding-top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main-pagina-producto .section__contenedor-producto .contenedor-producto__info-crypto-bitcoin .info-crypto-bitcoin__primer-dato-crypto {
  width: 60%;
  padding: 15px;
  display: flex;
  /* La propiedad justify-content space-between lo que hace
  es distribuir los elementos de manera uniforme
  El primer elemento está al ras con el inicio,
  el último está al ras con el final */
  justify-content: space-between;
  align-items: center;
}
.main-pagina-producto .section__contenedor-producto .contenedor-producto__info-crypto-bitcoin .info-crypto-bitcoin__segundo-dato-crypto {
  width: 60%;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main-pagina-producto .section__contenedor-producto .contenedor-producto__info-crypto-bitcoin .info-crypto-bitcoin__tercer-dato-crypto {
  width: 60%;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main-pagina-producto .section__contenedor-producto .contenedor-producto__info-crypto-bitcoin .info-crypto-bitcoin__cuarto-dato-crypto {
  width: 60%;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main-pagina-producto .section__contenedor-producto .cuarto-dato-crypto__enlace-evolucion-precio {
  padding: 5px;
  width: 96px;
  text-decoration: underline;
  background-color: rgb(204, 166, 31);
  font-family: "Staatliches";
  border-radius: 0.6em;
  color: black;
}

